<template>
  <div class="upload-img-wrap">
    <div class="sub-upload-wrap text-center">
      <template v-if="!bShowDragWrap">
        <h2 class="title">插入图片</h2>
        <i class="iconfont icon-close" @click.stop="hideUpload()"></i>
        <div class="progress-wrap">
          <p class="text">上传进度：</p>
          <div class="current-pro">
            <div class="current" :style="`width:${currentProgress}%`"></div>
          </div>
          <div>{{ currentProgress }}%</div>
        </div>
        <div class="select-img">
          <input type="file" name="file" value="" ref="inpFile" accept="image/png,image/gif,image/jpeg" @change.stop="showImgName($event)">
          <p class="mask">
            <span v-if="bFileMark"><i class="iconfont icon-upload-img2"></i>点击选择图片或者拖动图片到此窗口内</span>
            <template v-else>
              已选择：<img :src="previewUrl" alt="">
            </template>
          </p>
        </div>
        <div class="btn-wrap">
          <div v-if="bFileMark" class="btn btn-upload" @click.stop="uploadImg()">上传文件</div>
          <div class="btn btn-insert" @click.stop="insertImg()">插入到文章</div>
        </div>
        <div class="result-img">
          <img :src="resultImgUrl">
        </div>
      </template>
      <div v-else class="drag-wrap">
        <h2 class="title">松开鼠标完成上传</h2>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'uploadImg',
  data: () => ({
    currentProgress: 0,
    resultImgUrl: '',
    bFileMark: true,
    resFileName: '',
    bShowDragWrap: false,
    previewUrl: ''
  }),
  props: ['showChart'],
  computed: {
    ...mapState({
      contentUrl: state => state.info.blogInfo.contentUrl,
      templeteUrl: state => state.info.blogInfo.templeteUrl
    })
  },
  methods: {
    showImgName (event) {
      this.bFileMark = false
    },
    // 上传图片
    uploadImg () {
      let _file = this.$refs.inpFile
      if (_file.value) {
        // 上传实时进度
        let config = {
          onUploadProgress: progressEvent => (this.currentProgress = parseInt(progressEvent.loaded / progressEvent.total * 100))
        }
        let data = new FormData()
        if (_file.files[0].size / 1024 > 2048) {
          this.$message({
            title: '请上传小于2M的图片！',
            type: 'error'
          })
        } else {
          data.append('postID', this.$route.params.id)
          data.append('file', _file.files[0])
          data.append('url', this.contentUrl)
          data.append('mark', 'upload')
          window.axios.post(`/wp-content/themes/xm-vue-theme/xm_upload.php`, data, config).then(res => {
            this.resultImgUrl = res.data.path
            this.resFileName = res.data.name
            _file.value = ''
          }).catch(err => {
            if (err.response.status === 404) {
              this.$message({
                title: '上传失败(404)！',
                type: 'error'
              })
            }
          })
        }
      }
    },
    // 隐藏上传控件
    hideUpload () {
      let data = new FormData()
      data.append('mark', 'close')
      data.append('url', this.contentUrl)
      data.append('postID', this.$route.params.id)
      data.append('fileName', this.resFileName)
      // 如果本次上传的图片未发表就从服务器删除此图片
      window.axios.post(`/wp-content/themes/xm-vue-theme/xm_upload.php`, data).catch(err => console.log(err))
      // 关闭控件
      this.$emit('showChart', {
        close: false,
        resFileName: this.resFileName
      })
      this.currentProgress = 0
      this.resultImgUrl = ''
      this.bFileMark = true
      window.XM.removeClass(document.body, 'o-hide')
    },
    // 插入到文章
    insertImg () {
      this.$emit('updateContent', ` [img]${this.resultImgUrl}[/img] `)
      this.$emit('showChart', {
        close: false,
        resFileName: this.resFileName
      })
      this.currentProgress = 0
      this.resultImgUrl = ''
      this.bFileMark = true
      window.XM.removeClass(document.body, 'o-hide')
    }
  },
  mounted () {
    // 拖拽上传文件
    let oUploadWrap = document.querySelector('.sub-upload-wrap')
    oUploadWrap.ondragenter = (e) => {
      e.preventDefault()
      this.bShowDragWrap = true
    }
    oUploadWrap.ondragover = (e) => {
      e.preventDefault()
    }
    oUploadWrap.ondrop = (e) => {
      e.preventDefault()
      let oReader = new FileReader()
      let oFile = e.dataTransfer.files[0]
      this.bShowDragWrap = false
      // 判断文件是会否为图片格式
      if (oFile.type.indexOf('image') === -1) {
        this.$message({
          title: '请上传正确的图片格式',
          type: 'warning'
        })
        this.bFileMark = true
      } else {
        oReader.readAsDataURL(oFile)
        oReader.onload = () => {
          // 上传实时进度
          let config = {
            onUploadProgress: progressEvent => (this.currentProgress = parseInt(progressEvent.loaded / progressEvent.total * 100))
          }
          let data = new FormData()
          this.previewUrl = oReader.result
          this.bFileMark = false
          data.append('postID', this.$route.params.id)
          data.append('file', oReader.result)
          data.append('url', this.contentUrl)
          data.append('mark', 'upload')
          window.axios.post('/wp-content/themes/xm-vue-theme/xm_upload.php', data, config).then(res => {
            this.resultImgUrl = res.data.path
            this.resFileName = res.data.name
          }).catch(err => {
            if (err.response.status === 404) {
              this.$message({
                title: '上传失败(404)！',
                type: 'error'
              })
            }
          })
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
// 评论上传图片
.upload-img-wrap{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.7);

  .sub-upload-wrap{
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70%;
    min-height: 220px;
    padding: 10px;
    border-radius: 5px;
    background: #fff;
    transform: translate(-50%,-50%);
  }

  // 拖拽上传容器
  .drag-wrap{
    height: 200px;
    border: 2px dashed $colorGay1;
    line-height: 200px;
  }

  .title{
    font-size: 20px;
    font-weight: lighter;
  }

  .icon-close{
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
  }

  // 进度条
  .progress-wrap{
    display: flex;
    align-items: center;
  }

  .current-pro{
    width: 80%;

    .current{
      width: 0px;
      height: 5px;
      border-radius: 5px;
      background-color: $color-blue;
      background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, .3) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, .3) 50%, rgba(255, 255, 255, .3) 75%, transparent 75%, transparent);
    }
  }

  // 选择图片
  .select-img{
    position: relative;
    height: 50px;
    margin: 10px 0;
    border: 2px dashed $colorGay1;
    line-height: 50px;

    .mask,
    .iconfont{
      font-size: 18px;
    }

    .mask{
      span{
        overflow: hidden;
        display: block;
        text-overflow: ellipsis;
        white-space: nowrap;

        &.inline-block{
          display: inline-block;
          vertical-align: middle;
        }
      }

      img{
        max-width: 70px;
        vertical-align: middle;
      }
    }

    input{
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
    }
  }

  .result-img{
    img{
      max-width: 300px;
      max-height: 350px;
    }
  }

  .btn-wrap{
    display: flex;
    justify-content: space-between;
    width: 300px;
    margin: 0 auto;
    .btn{
      width: 100px;
      height: 40px;
      margin: 15px auto;
      border-radius: 5px;
      background: #0ae;
      line-height: 40px;
      color: #fff;
      cursor: pointer;
    }
  }
}
@media screen and (max-width: 767px) {
  .upload-img-wrap{
    .sub-upload-wrap{
      width: 90%;
    }
    .progress-wrap{
      flex-wrap: wrap;
      .text{
        width: 100%;
        text-align: left;
      }
      .current-pro{
        width: 90%;
      }
    }
    .result-img{
      img{
        max-width: 200px;
      }
    }
  }
}
@media screen and (max-height: 500px) {
  .upload-img-wrap{
    .result-img{
      img{
        max-width: 100px;
      }
    }
  }
}
</style>



